<template>
  <div id="login">
    <div class="login text-center">
      <div v-if="!user" class="login">
        <router-link to="/login"></router-link>
      </div>
      <div v-if="user" class="logout">
        <span class="name">{{user.name}}</span>  &nbsp;
        <router-link to="/cart">
          <i class="fas fa-shopping-cart"></i>
        </router-link>
        <br>
        <!-- sessionStorage user  ,  . -->
        <div class="logout btn" @click="logout"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: null
    };
  },
  methods: {
    logout() {
      sessionStorage.removeItem("user");
      location.reload();
    }
  },
  created() {
    // data methods       .
    // sessionStorage user  json  .
    this.user = JSON.parse(sessionStorage.getItem("user"));
  }
};
</script>

<style>
.login,
.login a {
  margin-top: 80px;
  color: gray;
  font-size: 1.5rem;
}

.name {
  color: #3d8e64;
}

a:hover {
  cursor: pointer;
  text-decoration: none;
  color: black;
}
.logout {
}
.logout .btn {
  color: gray;
}

.logout .btn:hover {
  cursor: pointer;
  color: black;
}
</style>



